<template>
  <div class="mvrankson">
    <div class="mvrankingson" @click="mvloc(item.id)">
      <img :src="`${item.cover}`" alt="" />
      <div>
        <p>{{ item.name }}{{item.id}}</p>
        <p>{{ item.artistName }}</p>
        <p>
          <span class="iconfont icon-video"></span
          >{{ item.playCount | formatNum }}
        </p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["item"],
  data() {
    return {};
  },
  methods: {
      mvloc(event){
          this.$router.push({path:'/mvranking',query:{mvId:event}})
          window.location.reload()
      }
  },
  filters: {
    formatNum(value) {
      if (value >= 10000) {
        return (parseInt(value) / 10000).toFixed(2) + "万";
      }
      return value;
    },
    formatdata(value) {
      if (value.length <= 0) {
        return "";
      }
      return value;
    },
  },
};
</script>
<style lang="less" scoped>
.mvrankson {
  width: 100%;
  .mvrankingson {
    width: 100%;
    margin: 3vw 0px 3vw 0;
    display: flex;
    img {
      border-radius: 3vw;
      width: 36vw;
      height: 22vw;
    }
    div {
      margin: 1vw 3vw 1vw 3vw;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      p {
        &:first-child {
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          font-size:4.4vw;
        }
        &:nth-child(2) {
          font-size: 2.4vw;
          color: #666;
        }
        &:nth-child(3) {
          font-size: 2.5vw;
          color: #666;
        }
      }
    }
  }
}
</style>