<template>
  <div class="mvranking">
    <div class="mvrankingson">
      <router-link
        class="fanhui iconfont icon-back_icon"
        tag="div"
        to="/"
      ></router-link>
      <video class="video" autoplay controls :src="`${newurl}`"></video>
      <ul>
        <li
          @click="piccomment = false"
          :style="{ color: piccomment == false ? '#0ecbf7' : '' }"
        >
          视频
        </li>
        <li
          @click="piccomment = true"
          :style="{ color: piccomment == true ? '#0ecbf7' : '' }"
        >
          评论
          <span>{{commentsall.length}}</span>
        </li>
      </ul>
      <div class="mvrankingimg" v-show="!piccomment">
        <div class="mvrankingimgson">
          <p>{{ name }}</p>
          <p>
            <span class="iconfont icon-video"></span>
            <span>{{ playCount | formatNum() }}</span>
            <span class="iconfont icon-time"></span>{{ publishTime }}
          </p>
        </div>
        <div class="jianjie">
          <div>
            简介:
            <div v-if="desc">
              <p>{{ desc | formatdata }}</p>
            </div>
          </div>
        </div>
        <div class="besimilar">
          <p>相似mv</p>
          <mv-rankingson
            v-for="mv in mvs"
            :key="mv.id"
            :item="mv"
            @picvideo="picvideo"
          ></mv-rankingson>
        </div>
      </div>
      <div class="commentses">
        <comment
          :hotComments="hotComments"
          :comments="comments"
          v-show="piccomment"
          :commentsall="commentsall"
          @Newcomment="Newcomment"
          :indexnum="indexnum"
          :mvstr="mvstr"
        ></comment>
      </div>
    </div>
    <div class="musicbottom"></div>
  </div>
</template>
<script>
import Comment from "@/components/Comment";
import MvRankingson from "@/components/MvRankingson";
export default {
  data() {
    return {
      newurl: "",
      name: "",
      publishTime: "",
      playCount: "",
      desc: "",
      hotComments: "",
      comments: "",
      commentsall: [],
      artistName: "",
      indexnum: 0,
      cover: "",
      mvs: "",
      mvstr:"按时间",
      piccomment: false,
      // usercomment: [],
    };
  },
  components: {
    Comment,
    MvRankingson,
  },
  beforeRouteEnter(to, from, next) {
    // ...
    next((vm) => {
      //mv视频
      vm.$axios
        .get(`/mv/url?id=${Number(vm.$route.query.mvId)}`)
        .then((data) => {
          vm.newurl = data.data.data.url;
          console.log(data);
        });
      //mv简介
      vm.$axios.get(`/mv/detail?mvid=${vm.$route.query.mvId}`).then((data) => {
        vm.name = data.data.data.name;
        vm.cover = data.data.data.cover;
        vm.artistName = data.data.data.artistName;
        vm.publishTime = data.data.data.publishTime;
        vm.playCount = data.data.data.playCount;
        vm.desc = data.data.data.desc;
        console.log(data);
      });
      //mv评论
      vm.$axios.get(`/comment/mv?id=${vm.$route.query.mvId}`).then((d) => {
        vm.comments = d.data.comments;
        vm.hotComments = d.data.hotComments;
        vm.commentsall = d.data.comments.concat(d.data.hotComments);
        console.log(vm.commentsall);
      });
      //相似mv
      vm.$axios.get(`/simi/mv?mvid=${vm.$route.query.mvId}`).then((d) => {
        vm.mvs = d.data.mvs;
        console.log(d);
      });
    });
  },
  methods: {
    picvideo(item) {
      console.log(item);
    },
    Newcomment() {
      function compare(property, desc) {
        return function (a, b) {
          var value1 = a[property];
          var value2 = b[property];
          if (desc == true) {
            // 升序排列
            return value1 - value2;
          } else {
            // 降序排列
            return value2 - value1;
          }
        };
      }
      // console.log(sdts.sort(compare("age",true)))
       if(this.indexnum == 0){
        this.indexnum = 1;
        this.mvstr="按热度"
        this.commentsall = this.commentsall.sort(compare("likedCount", false));
      }else if(this.indexnum == 1){
        this.indexnum = 0;
        this.mvstr="按时间"
        this.commentsall = this.commentsall.sort(compare("time", false));
      }
    },
    // Newcomment() {
    //   this.indexnum = 0;
    //   function compare(property, desc) {
    //     return function (a, b) {
    //       var value1 = a[property];
    //       var value2 = b[property];
    //       if (desc == true) {
    //         // 升序排列
    //         return value1 - value2;
    //       } else {
    //         // 降序排列
    //         return value2 - value1;
    //       }
    //     };
    //   }
    //   // console.log(sdts.sort(compare("age",true)))
    //   this.commentsall = this.commentsall.sort(compare("time", false));
    // },
    // Hotcomment() {
    //   this.indexnum = 1;
    //   function compare(property, desc) {
    //     return function (a, b) {
    //       var value1 = a[property];
    //       var value2 = b[property];
    //       if (desc == true) {
    //         // 升序排列
    //         return value1 - value2;
    //       } else {
    //         // 降序排列
    //         return value2 - value1;
    //       }
    //     };
    //   }
    //   // console.log(sdts.sort(compare("age",true)))
    //   this.commentsall = this.commentsall.sort(compare("likedCount", false));
    // },
    //点赞功能
    // likedcount(event) {
    //   //数组去重
    //   if (Array.isArray(this.usercomment)) {
    //     this.usercomment.push(event)
    //     let arr = []
    //     for (var i = 0; i < this.usercomment.length; i++) {
    //       if (arr.indexOf(this.usercomment[i]) === -1) {
    //         arr.push(this.usercomment[i]);
    //     console.log(arr)
    //       }
    //     }
    //     this.usercomment= arr
    //   }
    // },
  },
  filters: {
    formatNum(value) {
      if (value >= 10000) {
        return (parseInt(value) / 10000).toFixed(2) + "万";
      }
      return value;
    },
    formatdata(value) {
      if (value.length <= 0) {
        return "";
      }
      return value;
    },
  },
};
</script>
<style lang="less" scoped>
.mvranking {
  width: 100%;
  .mvrankingson {
    width: 100%;
    .fanhui {
      position: absolute;
      top: 10px;
      left: 10px;
      color: white;
      font-weight: 600;
      font-size: 20px;
      z-index: 9999;
    }
    .video {
      width: 100%;
    }
    ul {
      line-height: 30px;
      background-color: white;
      display: flex;
      border-bottom: 1px solid #ccc;
      li {
        font-size:4vw;
        margin: 5px 10px;
        &:nth-child(2) {
          margin-left: 20px;
        }
        &.router-link-exact-active {
          background-color: #c7c6c6;
          color: #dd001b;
          position: relative;
        }
        span{
          font-size:3vw;
          color: #666;
        }
      }
    }
    .mvrankingimg {
      .mvrankingimgson {
        p {
          &:first-child {
            font-weight: 600;
            font-size: 18px;
            margin: 10px;
          }
          &:nth-child(2) {
            font-size: 13px;
            color: #666;
            span {
              margin: 10px;
              font-size: 14px;
              &:first-child {
                margin-right: 2px;
              }
              &:nth-child(2) {
                margin-left: 0;
              }
              &:nth-child(3) {
                margin-right: 3px;
              }
            }
          }
        }
      }
      .jianjie {
        div {
          margin: 10px 10px 0px 10px;
          p {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            font-size: 14px;
            text-indent: 2em;
          }
        }
      }
      .besimilar {
        padding: 10px;
        p {
          border-top: 1px solid #ddd;
          font-weight: 600;
          padding: 10px 0 10px 0;
        }
      }
    }
    .commentses {
      margin: 10px;
    }
  }
  .musicbottom {
    width: 100%;
    height: 50px;
  }
}
</style>