<template>
  <div class="comment">
    <p class="newcomment">
      <span @click="$emit('Newcomment')" >{{mvstr}}</span>
    </p>
    <ul v-for="(item, index) in commentsall" :key="index">
      <div>
        <li>
          <img :src="item.user.avatarUrl" alt="" />
        </li>
        <li>
          <p>{{ item.user.nickname }}</p>
          <p>{{ item.time | formatDate("Y年m月d日") }}</p>
          <p>
            {{ item.content }}
          </p>
        </li>
      </div>
      <!-- <div>
        <span class="iconfont icon-dianzan"
         @click="$emit('likedcount',{usercommentId:item.commentId,userlikedCount:item.likedCount})"
         :style="{color:item.commentId==usercomment[index]? 'red':''}"></span>
        <span class="fa fa-thumbs-o-up">&nbsp;{{item.likedCount | formatNum()}}</span>
      </div> -->
      <div>
        <span class="iconfont icon-dianzan"></span>
        <span class="fa fa-thumbs-o-up">&nbsp;{{item.likedCount | formatNum()}}</span>
      </div>
    </ul>
  </div>
</template>
<script>
export default {
  props: ["hotComments", "comments","commentsall","indexnum","mvstr"],
  data() {
    return {
      // hotdata: [],
      i: 0,
      isred:false
    };
  },
  methods: {
    // Hot() {
    //   this.hotdata = this.hotComments;
    //   this.i = 1;
    // },
    // New() {
    //   this.hotdata = this.comments;
    //   this.i = 0;
    // },
  },
  watch: {
    comments(event) {
      console.log(event);
      //this.hotdata = event;
    },
  },
  filters: {
    formatDate(value, fstr) {
      let d = new Date(value);
      let formatO = {
        "Y+": d.getFullYear(),
        "m+": d.getMonth() + 1,
        "d+": d.getDate(),
        "h+": d.getHours(),
        "M+": d.getMinutes(),
        "S+": d.getSeconds,
      };
      for (let k in formatO) {
        let reg = new RegExp(k);
        let val = formatO[k];
        val = val < 10 ? "0" + val : val;
        fstr = fstr.replace(reg, val);
      }
      return fstr;
    },
    formatNum(value) {
      if (value >= 10000) {
        return (parseInt(value) / 10000).toFixed(0) + "万";
      }
      return value;
    },
  },
};
</script>
<style lang="less" scoped>
.comment {
  & > p {
    color: #666;
    padding: 5px;
    span {
      padding: 5px;
      font-size: 4vw;
    }
  }
  ul {
    padding: 5px;
    border-bottom: 1px solid #ccc;
    display: flex;
    div {
      &:first-child {
        width: 80%;
        display: flex;
        li {
          &:first-child {
            width: 14vw;
            height: 14vw;
            text-align: center;
            border: 2px solid #4fd1ef;
            box-sizing: border-box;
            border-radius: 50%;
            overflow: hidden;
            img {
              width: 100%;
              height: 100%;
            }
          }
          &:nth-child(2) {
            padding: 5px;
            width: 80%;
            p {
              &:first-child {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                font-size: 4.2vw;
              }
              &:nth-child(2) {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                font-size: 3vw;
                color: #999;
              }
              &:last-child {
                margin: 5px 0;
                font-size: 4.2vw;
                width: 100%;
                word-wrap: break-word;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
            }
          }
        }
      }
      &:last-child {
        width: 20%;
        font-size:4vw;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-top: 5px;
        i {
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
  }
}
</style>